.heading {
    text-align: center;
}

.heading h2 {
    font-size: 30px;
    color: white;
    margin-bottom: 20px;
}

.heading span {
    font-size: var(--p-font);
    color: rgb(2, 166, 70);
    font-size: 20px;
    margin-bottom:20px;
}
.skills-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, auto));
    justify-content: center;
    gap: 1rem;
    margin-top:10px;
}
.skills-card {
    padding: 8px;
    width: 150px;
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border:1px solid blue;
    background-color: white;
    border-radius: 2px;
    border-bottom: 2px solid var(--main-color);
    box-shadow: 2 2px 7px rgba(14, 17, 55, 0.15);
}

.skills-card:hover {
    background-color: #02225a;
    color: #fff;
    cursor: pointer;
}

#skills img {
    width: 60%;
    aspect-ratio: 4/4;
}

@media (max-width: 768px) {

.skills-container {
    grid-template-columns: 1fr;
}
}